
    .experience-main{
        background-color: inherit;
        height: 100vh;
    }


    .items {
      position: relative;
      width: 100%;
      overflow-x: scroll;
      overflow-y: visible;
      white-space: nowrap;
      transition: all 0.2s;
      will-change: transform;
      user-select: none;
      cursor: pointer;
      text-align:center;
      padding-inline-start: 50%;
      padding-inline-end: 50%;
      scrollbar-width: none;
    }

    .items::-webkit-scrollbar{
        display: none;
      }

    .items.active {
      background: rgba(255, 255, 255, 0.3);
      cursor: grabbing;
      cursor: -webkit-grabbing;
    }

    .drag-item {
      display: inline-block;
      /* background-color: black; */
      min-height: 100%;
      min-width: 50vw;
      top:20%;
      margin-right: 20vw;
    }

    .drag-item .circle{
        width: 2.5rem;
        height: 2.5rem;
        top:15%;
        background-color:blue;
        transition: all 1s ease;
        transform: translateY(-50%) translateX(-50%);
        border-radius: 10px;
    }

    .drag-item.active .circle{
        width: 5rem;
        height: 5rem;
        border-radius: 10px;
    }

    .drag-item .square{
        position: absolute;
        height: 0%;
        width: 0%;
        border-radius: 20px;
        transition: all 1s ease;
        overflow-x: clip;
        top: 15%;
        background: rgba(131, 167, 239, 0.12);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(0px);
        -webkit-backdrop-filter: blur(0px);
    }

    .drag-item.active .square{
        opacity: 1;
        transform: translateX(0%) translateY(0%) ;
    }


    .drag-line{
        position: absolute; 
        height: 0.125rem;
        border-radius: 9999px;
        left: 0; 
        right: 0; 
        margin-left: 50%; 
        margin-right: 50%; 
        top:15%;
    }

    .drag-item .drag-title{
        position: absolute;
        color: white;
        text-align: left;
        top:15%;
        z-index: 10;
        transform: translateY(-80%) translateX(1.5rem);
        transition: all 1s ease;
        overflow: hidden;
text-overflow: ellipsis;
white-space: nowrap;
    }

    .drag-item.active .drag-title{
        transform: translateY(-80%) translateX(2.75rem);
    }

    .drag-item .drag-date{
        position: absolute;
        color: white;
        text-align: left;
        /* left: 10px; */
        top:15%;
        z-index: 10;
        transform: translateY(0%) translateX(1.5rem);
        transition: all 1s ease;
    }

    .drag-item.active .drag-date{
        transform: translateY(0%) translateX(2.75rem);
    }

    .drag-item .drag-text-body{
        color: white;
        text-align: left;
        white-space: initial;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        
        opacity: 0;
    }

    .drag-item.active .drag-text-body{
        opacity: 1;
        transform: translateY(2.5rem) translateX(2.75rem);
        width: calc(100% - 2.75rem);
        height: calc(100 - 2.5rem - 10%);
        transition: opacity 1s 1s ease;
        /* text-overflow: clip; */
    }

    
    /* drag-pill group */
    .drag-item .drag-pill-group{
        position: absolute;
        opacity: 0;
        bottom: 10%;
    }
    
    .drag-item.active .drag-pill-group{
        opacity: 1;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        transition: opacity 2s 0.5s ease;
    }    

    /* pill */
    .drag-pill-group .drag-pill{
        position: relative;
        display: flex;
        flex-direction: row;
        background-color: white;
        border-radius: 20px;
        height: 2rem;
        max-width: 2rem;
        overflow: hidden;
        padding-left: 0.2rem;
    }
    .drag-pill-group .drag-pill:hover{
        max-width: 100%;
        transition: max-width 1s ease;
        padding-right: 0.2rem;
    }

    .drag-pill:not(:first-child){
        margin-left: 0.125rem;
    }

    /* pill image */
    .drag-pill-group .drag-pill .pill-image{
        max-width: 1.6rem;
        max-height: 1.6rem;
        border-radius: 20px;
        margin-top: auto;
        margin-bottom: auto;
    }

    /* Pill text */
    .drag-pill-group .drag-pill .pill-text{
        font-size: 1rem;
        transform-origin: left;
        transition: all 2s ease;
        margin-left: 0.3rem;
        margin-top: auto;
        margin-bottom: auto;
    }

    .drag-pill-group .drag-pill:hover .pill-text{
        display: block;
        
    }


    /* Media default */
    .items {
        height: 80%;
        background-color: blueviolet;
        overflow-y: hidden;
    }
    .drag-item .drag-title{
        font-size: 1.5rem;
    }
    .drag-item .drag-date{
        font-size: 1.5rem;
    }
    .drag-item.active .drag-text-body{
        font-size: 1rem;;
        -webkit-line-clamp: unset;
        transform: translateY(2.5rem) translateX(2.5rem);
        width: calc(100% - 2.6rem);
    }

    .drag-item.active .square{
        height: 60%;
        width: 50%;
        transform: translateX(0%) translateY(0%) ;
    }

    .drag-item .drag-pill-group{
        left: 2.75rem;
    }
      

    

    @media (min-width: 0px) and (max-width: 640px) {
        .items{
            height: 50%;
            background-color: blueviolet;
        }
        .drag-item .drag-title{
            font-size: 0.8rem;
        }
        .drag-item .drag-date{
            font-size: 0.8rem;
        }
        .drag-item.active .drag-text-body{
            font-size: 0.8rem;
            -webkit-line-clamp: unset;
            transform: translateY(2.5rem) translateX(1rem);
            width: calc(100% - 1.1rem);
        }

        .drag-item.active .square{
            width: 60%;
            transform: translateX(0%) translateY(0%) ;
        }

        .drag-item .drag-pill-group{
            left: 1rem;
        }
      }
      
